<template>
  <v-container>
    <v-row>
        <v-col cols="12">
          <h1 class="display-2 font-weight-bold mb-3">Softball Dashboard</h1>
        </v-col>
    </v-row>

    <v-row>
        <v-col cols="12">
          <h3>Men's Stats</h3>
        </v-col>
    </v-row>

    <v-row class="pt-5">
      <v-col cols="12" md="3" lg="2">
        <v-card>
          <v-card-text style="text-align: center">
            <div>Record</div>
            <div class="text-h4 text--primary pt-6">{{menwins}} - {{menlosses}}</div>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="6" md="3" lg="2">
        <v-card>
          <v-card-text>
            <div>Home Runs</div>
            <v-simple-table dense>
              <tbody>
                <tr>
                  <td style="text-align: right">{{meninsidehrs}}</td>
                  <td>Inside the Park</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{menoutsidehrs}}</td>
                  <td>Over the Fence</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{mentotalhrs}}</td>
                  <td>Total Home Runs</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="6" md="3" lg="2">
        <v-card>
          <v-card-text>
            <div>Run Differential</div>
            <v-simple-table dense>
              <tbody>
                <tr>
                  <td style="text-align: right">{{menrunsfor}}</td>
                  <td>Runs For</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{menrunsagainst}}</td>
                  <td>Runs Against</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="12" lg="5">
        <v-card>
          <v-card-text>
            <div>Next Game Day</div>
            <v-data-table
              :headers="headers"
              :items="menupcoming"
              :sort-by="['gamedate', 'gametime']"
              :sort-desc="[false, false]"
              multi-sort
              disable-pagination
              hide-default-footer
              no-data-text="Season complete!"
            ></v-data-table>
          </v-card-text>
          <v-card-actions>
            <v-btn color="button_color" text to="/2023-Spring-Men">Full Schedule</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>

    <v-row class="pt-5">
      <v-col cols="12">
        <hr/>
      </v-col>
    </v-row>

    <v-row>
        <v-col cols="12">
          <h3>Coed Stats</h3>
        </v-col>
    </v-row>

    <v-row class="pt-5">
      <v-col cols="12" md="3" lg="2">
        <v-card>
          <v-card-text style="text-align: center">
            <div>Record</div>
            <div class="text-h4 text--primary pt-6">{{coedwins}} - {{coedlosses}}</div>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="6" md="3" lg="2">
        <v-card>
          <v-card-text>
            <div>Home Runs</div>
            <v-simple-table dense>
              <tbody>
                <tr>
                  <td style="text-align: right">{{coedinsidehrs}}</td>
                  <td>Inside the Park</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{coedoutsidehrs}}</td>
                  <td>Over the Fence</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{coedtotalhrs}}</td>
                  <td>Total Home Runs</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="6" md="3" lg="2">
        <v-card>
          <v-card-text>
            <div>Run Differential</div>
            <v-simple-table dense>
              <tbody>
                <tr>
                  <td style="text-align: right">{{coedrunsfor}}</td>
                  <td>Runs For</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{coedrunsagainst}}</td>
                  <td>Runs Against</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="12" lg="5">
        <v-card>
          <v-card-text>
            <div>Next Game Day</div>
            <v-data-table
              :headers="headers"
              :items="coedupcoming"
              :sort-by="['gamedate', 'gametime']"
              :sort-desc="[false, false]"
              multi-sort
              disable-pagination
              hide-default-footer
              no-data-text="Season complete!"
            ></v-data-table>
          </v-card-text>
          <v-card-actions>
            <v-btn color="button_color" text to="/2023-Spring-CoEd">Full Schedule</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>

  </v-container>
</template>

<script>
  import coedjson from "@/assets/coed-schedule.json";
  import menjson from "@/assets/mens-schedule.json";
  import moment from 'moment';

  export default {
    name: 'HelloWorld',
    data() {
      return {
      headers: [
        { text: "Game Date", value: "gamedate"},
        { text: "Opponent", value: "opponent" },
        { text: "Game Time", value: "gametime", align: "center" },
        { text: "Field", value: "field", align: "center" },
      ],
        mensgames: [],
        coedgames: [],
        menupcoming: [],
        coedupcoming: [],
        menwins: 0,
        menlosses: 0,
        menrunsfor: 0,
        menrunsagainst: 0,
        meninsidehrs: 0,
        menoutsidehrs: 0,
        mentotalhrs: 0,
        coedwins: 0,
        coedlosses: 0,
        coedrunsfor: 0,
        coedrunsagainst: 0,
        coedinsidehrs: 0,
        coedoutsidehrs: 0,
        coedtotalhrs: 0
      }
    },
    mounted() {
      this.loadData();
    },
    methods: {
      loadData() {
        this.mensgames = menjson.softballgames;
        this.coedgames = coedjson.softballgames;

        for (let i = 0; i < coedjson.softballgames.length; i++) 
        {
          if(coedjson.softballgames[i].result == "W") this.coedwins++;
          if(coedjson.softballgames[i].result == "L") this.coedlosses++;
          if(coedjson.softballgames[i].ourscore != "") this.coedrunsfor += parseInt(coedjson.softballgames[i].ourscore);
          if(coedjson.softballgames[i].theirscore != "") this.coedrunsagainst += parseInt(coedjson.softballgames[i].theirscore);
          if(coedjson.softballgames[i].insidehrs != "") 
          {
            this.coedinsidehrs += parseInt(coedjson.softballgames[i].insidehrs);
            this.coedtotalhrs += parseInt(coedjson.softballgames[i].insidehrs);
          }
          
          if(coedjson.softballgames[i].outsidehrs != "") 
          {
            this.coedoutsidehrs += parseInt(coedjson.softballgames[i].outsidehrs);
            this.coedtotalhrs += parseInt(coedjson.softballgames[i].outsidehrs);
          }
        }

        for (let i = 0; i < menjson.softballgames.length; i++) 
        {
          if(menjson.softballgames[i].result == "W") this.menwins++;
          if(menjson.softballgames[i].result == "L") this.menlosses++;
          if(menjson.softballgames[i].ourscore != "") this.menrunsfor += parseInt(menjson.softballgames[i].ourscore);
          if(menjson.softballgames[i].theirscore != "") this.menrunsagainst += parseInt(menjson.softballgames[i].theirscore);
          if(menjson.softballgames[i].insidehrs != "") 
          {
            this.meninsidehrs += parseInt(menjson.softballgames[i].insidehrs);
            this.mentotalhrs += parseInt(menjson.softballgames[i].insidehrs);
          }
          
          if(menjson.softballgames[i].outsidehrs != "") 
          {
            this.menoutsidehrs += parseInt(menjson.softballgames[i].outsidehrs);
            this.mentotalhrs += parseInt(menjson.softballgames[i].outsidehrs);
          }
        }

        //Get Current Moment
        const currentMoment = moment();
        //Set Moment to current date
        const currentDay = moment().set({'year': currentMoment.year(), 'month': currentMoment.month(), 'date': currentMoment.date(), 'hour': 1, 'minute': 0, 'second': 0});
        currentDay.subtract(1, 'days');
        let nextMenGame;

        for (let i = 0; i < menjson.softballgames.length; i++) 
        {
          let myGameDate = moment(menjson.softballgames[i].gamedate, "MM/DD/YYYY");
          if(currentDay.isSameOrBefore(myGameDate))
          {
            nextMenGame = myGameDate;
            break;
          }
        }

        for (let i = 0; i < menjson.softballgames.length; i++) 
        {
          if(nextMenGame != undefined && nextMenGame.isSame(moment(menjson.softballgames[i].gamedate, "MM/DD/YYYY")))
          {
            this.menupcoming.push(menjson.softballgames[i]);
          }
        }

        let nextCoEdGame;
        for (let i = 0; i < coedjson.softballgames.length; i++) 
        {
          let myGameDate = moment(coedjson.softballgames[i].gamedate, "MM/DD/YYYY");
          if(currentDay.isSameOrBefore(myGameDate))
          {
            nextCoEdGame = myGameDate;
            break;
          }
        }

        for (let i = 0; i < coedjson.softballgames.length; i++) 
        {
          if(nextCoEdGame != undefined && nextCoEdGame.isSame(moment(coedjson.softballgames[i].gamedate, "MM/DD/YYYY")))
          {
            this.coedupcoming.push(coedjson.softballgames[i]);
          }
        }

      },
    }
  }
</script>
