<template>
  <v-app>
    <v-app-bar
      app
      color="toolbar_color"
      dark
    >
      <v-app-bar-nav-icon @click="drawer = true"></v-app-bar-nav-icon>
      <v-toolbar-title>Softball Dashboard</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn v-if="!$vuetify.theme.dark" icon @click="toggle_dark_mode">
        <v-icon>mdi-weather-night</v-icon>
      </v-btn>
      <v-btn v-if="$vuetify.theme.dark" icon @click="toggle_dark_mode">
        <v-icon>mdi-white-balance-sunny</v-icon>
      </v-btn>
    </v-app-bar>

    <v-navigation-drawer
      v-model="drawer"
      fixed
      temporary
    >

    <v-list
        nav
        dense
      >
        <v-list-item-group
          v-model="group"
        >
          <v-list-item link to="/">
            <v-list-item-icon>
              <v-icon>mdi-home</v-icon>
            </v-list-item-icon>
            <v-list-item-title>Home</v-list-item-title>
          </v-list-item>

          <v-list-item link to="/2023-Spring-CoEd">
            <v-list-item-icon>
              <v-icon>mdi-baseball</v-icon>
            </v-list-item-icon>
            <v-list-item-title>2023 Spring CoEd</v-list-item-title>
          </v-list-item>

          <v-list-item link to="/2023-Spring-Men">
            <v-list-item-icon>
              <v-icon>mdi-baseball-bat</v-icon>
            </v-list-item-icon>
            <v-list-item-title>2023 Spring Men</v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>

    </v-navigation-drawer>

    <v-main>
      <router-view/>
    </v-main>
  </v-app>
</template>

<script>

export default {
  name: 'App',

  data: () => ({
    drawer: false,
    group: null,
  }),
  methods: {
    toggle_dark_mode: function() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
    }
  },
  mounted() {
    if(window.matchMedia && window.matchMedia("(prefers-color-scheme: dark)").matches)
    {
      this.$vuetify.theme.dark = true;
    }
  },
};
</script>
